import { Component, Input, OnInit, Output } from '@angular/core';
import { EventEmitter } from '@angular/core';
import * as rumble from '../../../shared/models/rumble';
import { buffAppliesToUnit } from '../../../core/utils/effects';

@Component({
  selector: 'app-unit-card',
  templateUrl: './unit-card.component.html',
  styleUrls: ['./unit-card.component.css']
})
export class UnitCardComponent implements OnInit {

  @Input()
  public unit: rumble.Unit;

  @Input()
  set teamEffects(value: rumble.Effect[]) {
    this.updateBuffs(value);
  }

  @Output()
  public unitClick = new EventEmitter<boolean>();

  defaultImage = 'assets/images/recruit_wanted.png';
  buffs: {
    hp: number,
    atk: number,
    def: number,
    rcv: number,
    spd: number,
  };

  constructor() {
    this.resetBuffs();
  }

  private resetBuffs(): void {
    this.buffs = {
      hp: 0,
      atk: 0,
      def: 0,
      rcv: 0,
      spd: 0,
    };
  }

  private updateBuffs(teamEffects: rumble.Effect[]): void {
    this.resetBuffs();
    teamEffects.forEach(e => {
      const applies = buffAppliesToUnit(e, this.unit);
      if (!applies) { return; }
      e.attributes.forEach(a => {
        switch (a) {
          case 'HP':
            this.buffs.hp += e.level;
            break;
          case 'ATK':
            this.buffs.atk += e.level;
            break;
          case 'DEF':
            this.buffs.def += e.level;
            break;
          case 'RCV':
            this.buffs.rcv += e.level;
            break;
          case 'SPD':
            this.buffs.spd += e.level;
            break;
        }
      });
    });
  }

  ngOnInit(): void {
    console.log('unit', this.unit);
  }

  onClick(): void {
    this.unitClick.emit(true);
  }

}
