import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { UnitTableDataSource } from 'src/app/database/components/unit-table/unit-table-datasource';
import * as rumble from '../../../shared/models/rumble';
import { UnitPickerComponent } from '../../components/unit-picker/unit-picker.component';
import { buffAppliesToTime } from '../../../core/utils/effects';
import { battleTime as maxBattleTime } from '../../../core/constants/battle';

const mainTeamSize = 5;
const subTeamSize = 3;

@Component({
  selector: 'app-team-builder',
  templateUrl: './team-builder.component.html',
  styleUrls: ['./team-builder.component.css']
})
export class TeamBuilderComponent implements OnInit {

  battleTime = maxBattleTime;
  mainTeam: rumble.Unit[] = [];
  subTeam: rumble.Unit[] = [];
  units: rumble.Unit[];
  battleTimer: number;
  teamEffects: rumble.Effect[] = [];
  totals: {
    cost: number,
  };

  constructor(
    private dialog: MatDialog,
    dataSource: UnitTableDataSource
  ) {
    this.battleTimer = this.battleTime;
    this.totals = {
      cost: 0,
    };
    this.units = dataSource.data;
    for (let index = 1; index <= mainTeamSize; index++) {
      this.mainTeam.push(null);
    }
    for (let index = 1; index <= subTeamSize; index++) {
      this.subTeam.push(null);
    }
  }

  ngOnInit(): void {
  }

  updateCost(): void {
    // update totals.cost based on main/sub team current state
  }

  updateBuffs(): void {
    this.teamEffects = this.mainTeam
      .filter(unit => unit != null)
      .flatMap(unit => unit.lvl5Ability)
      .filter(e => e.effect === 'buff' && buffAppliesToTime(e, this.battleTime))
    ;
  }

  updateDebuffs(): void {
    // update totals.debuffs based on main team current state
  }

  mainTeamUnitClick(index: number): void {
    console.log('unit clicked', index);
    this.openUnitPicker((data) => {
      this.mainTeam[index] = data;
      this.updateBuffs();
      this.updateDebuffs();
      this.updateCost();
    });
  }

  subTeamUnitClick(index: number): void {
    console.log('sub unit clicked', index);
    this.openUnitPicker((data) => {
      this.subTeam[index] = data;
      this.updateCost();
    });
  }

  openUnitPicker(onPick: (unit: rumble.Unit) => void): void {
    const dialogConfig = new MatDialogConfig();

    dialogConfig.hasBackdrop = true;
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;

    dialogConfig.data = {
      units: this.units
    };

    const dialogRef = this.dialog.open(UnitPickerComponent, dialogConfig);

    dialogRef.afterClosed().subscribe(
      data => {
        if (data !== undefined) {
          // allow null so we can unset units
          onPick(data);
        }
      }
    );
  }

}
